/**
 * createStore
 *  redux store
 *
 * @param reducer      
 * @param initialState 
 * @param enhancer      applyMiddlewares 
 * @return store
 */
const createStore = (reducer, initialState, enhancer) => {
  if (typeof initialState === 'function' && typeof enhancer === 'undefined') {
    enhancer = initialState;
    initialState = undefined;
  }

  // middlewares
  if (enhancer) {
    let e = enhancer(createStore(reducer, initialState));
    console.log('[redux]', e);
    return e;
  }

  const store = {};
  store.state = initialState;
  store.listeners = [];

  store.subscribe = (listener) => {
    store.listeners.push(listener);
  };

  store.dispatch = (action) => {
    store.state = reducer(store.state, action);
    store.listeners.forEach(listener => listener());
  };
  store.getState = () => store.state;
  return store;
};

// 
// Redux middleware  action  middleware 
//  action, action  action

//  ( dispatch)
// const logger = ({ dispatch, getState }) => action => {
//   console.log(action, getState());
//   return dispatch(action);
// };
// export default logger;

export function applyMiddleware(middleware) {
  return store => {

    const dispatch = middleware({
      getPrevState: store.getState,
      dispatch: store.dispatch
    });

    return {
      ...store,
      dispatch
    };
  };
}

/**
 * 
 * @param middlewares
 * @return {function(*)}
 */
// const logger = ({ dispatch, getPrevState }) => next => action => {
//   console.log('[redux-logger] ', action, getPrevState());
//   return next(action);
// };
export function applyMiddlewares(...middlewares) {
  return store => {

    const middlewareChain = middlewares.map(middleware =>
      middleware({
        getPrevState: store.getState,
        dispatch: store.dispatch
      }));

    const operator = compose(middlewareChain);
    const dispatch = operator(store.dispatch);

    return {
      ...store,
      dispatch
    };
  };
}

/**
 * compose
 *
 * @param fns [fn1, fn2]
 * @return {*}
 */

function compose(fns) {
  if (fns.length === 1) {
    return fns[0];
  }
  return fns.reduce((ret, middleware) => dispatch => ret(middleware(dispatch)));
}

export default createStore;

